
@use "@/assets/scss/_base";

:deep(.main) {
    display: flex;
}

.main-content {
    flex: 1 0 auto;
}

.sidebar {
    height: 560px;

    @include base.column-padding;
}

.map {
    @include base.column-padding;
}

@media (max-width: base.$screen-sm-max) {
    .sidebar {
        width: 100%;
    }

    .map {
        width: 100%;
        height: 100vh;
        height: 100svh;
    }
}

@media (min-width: base.$screen-md-min) {
    .sidebar {
        width: 400px;
    }

    .map {
        flex: 1 0 auto;
    }
}
